h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}













:root {
  --black-header-color: #111;
  --black-body-color: #202024;
  --violet-color: #4f2ee8;
  --white-color: #fff;
  --grey-text-color: #b4afaf;
  --grey-categories-color: #e0e0e0;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--black-header-color);
  letter-spacing: -.04em;
  background-color: #f6f6f6;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.container {
  max-width: 375px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

html {
  scroll-behavior: smooth;
}

.no-scroll {
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

